import React, {useState} from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { AppBar, Box, Tab, Tabs, TextareaAutosize, Typography } from '@material-ui/core';
import { DataMatrixPlus, ContentSaveCog } from 'mdi-material-ui'
import { Dropzone } from './Dropzone'

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export function AddTabs(props) {
  
  
  const classes = useStyles();
  const [value, setValue] = useState(0);
  const [textareaValue, setTextareaValue] = useState('')

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const change = (event) => {
    setTextareaValue(event.target.value);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs value={value} onChange={handleChange} variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs example" centered>
          <Tab label="Collate &amp; Save" {...a11yProps(0)} icon={<ContentSaveCog />} />
          <Tab label="Create Matrix" {...a11yProps(1)} icon={<DataMatrixPlus /> }/>
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <TextareaAutosize
          label='copy paste fractal matrix'
          rowsMin={7}          
          value={textareaValue}
          onChange={change}
          margin='normal'
          variant='outlined'        
          spellCheck='false'
          className={classes.textareaAutosize} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Dropzone onClose={() => props.onClose()} /> 
      </TabPanel>      
    </div>
  );
}